'use strict';

angular.module('videoHub')
  .controller('SliderDetailController', function ($scope, $q, $routeParams, AlertService,
      SliderService, DetailPageControllerConstants, Restangular) {

    var normalizeSlideIndicies = function () {
      // update indicies when sortable is updated
      _.each($scope.slider.slides, function (slide, i) {
        slide.position = i;
      });
    };

    $scope.slider = {};
    $scope.$getSlider = function (sliderId) {
      return SliderService.one(sliderId).get()
        .then(function(data) {
          $scope.slider = data;
          normalizeSlideIndicies();
        })
        .catch(function(data) {
          console.log(data);
        });
    };

    // ui-sortable setup
    $scope.sortableOptions = {
      containment: 'document',
      opacity: 0.75,
      placeholder: 'dropzone',
      stop: function () {
        $scope.sliderDetailForm.$setDirty();
        normalizeSlideIndicies();
      }
    };

    /**
     * Add a new slide.
     */
    $scope.addSlide = function () {
      if (!_.isArray($scope.slider.slides)) {
        // ensure slider slides list is initialized
        $scope.slider.slides = [];
      }

      $scope.slider.slides.push({});
      $scope.sliderDetailForm.$setDirty();
      normalizeSlideIndicies();
    };

    /**
     * Remove a slide.
     *
     * @param {Object} slide - slide to remove from slider.
     */
    $scope.removeSlide = function (slide) {
      if (slide) {
        var i = _.findIndex($scope.slider.slides, slide);
        if (i >= 0) {
          $scope.slider.slides.splice(i, 1);
          $scope.sliderDetailForm.$setDirty();
          normalizeSlideIndicies();
        }
      }
    };

    /**
     * Save changes to slider.
     *
     * @returns {Promise} resolves based on {@link AlertService}.
     */
    $scope.$saveSlider = function () {
      var promise;

      if ($scope.sliderDetailForm.$valid) {
        // form is valid, attempt to save
        promise = $scope.slider.save()
          .then(function () {
            $scope.sliderDetailForm.$setPristine();
            // success in saving, display success alert
            return AlertService.success(
                DetailPageControllerConstants.SAVED_ALERT_MESSAGE,
                true);
          })
          .catch(function () {
            return AlertService.error(
              DetailPageControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE,
              true);
          });
      } else {
        // form is not valid, reject saving with an alert
        promise = AlertService.error(
            DetailPageControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE,
            true);
      }

      return promise;
    };

    $scope.$deleteSlider = function () {
      return AlertService.info('If you would like to delete a slider, please contact your friendly sysadmin.', true);
    };

    // set up
    if (_.has($routeParams, 'sliderId')) {
      // exising slider, use route id
      $scope.$getSlider($routeParams.sliderId);
    } else {
      // this is a new slider
      Restangular.restangularizeElement(null, $scope.slider, 'sliders');
    }
  });
