'use strict';
angular.module('videoHub')
  .constant('SAVE_BUTTON_STATES', {
    'SAVING': 'saving',
    'SAVED': 'saved'
  })
  .service('SaveButtonStateService', function($timeout, $q, SAVE_BUTTON_STATES) {
    this.data = {};
    this.data.saveButtonState = '';

    var SaveButtonStateService = this;

    var setStateToSaving = function() {
      SaveButtonStateService.data.saveButtonState = SAVE_BUTTON_STATES.SAVING;
    };

    var setStateToSaved = function() {
      SaveButtonStateService.data.saveButtonState = SAVE_BUTTON_STATES.SAVED;
    };

    var clearState = function() {
      SaveButtonStateService.data.saveButtonState = '';
    };
    this.displaySavingAnimation = function() {
      var defer = $q.defer();
      setStateToSaving();
      $timeout(setStateToSaved, 2000)
        .then(function() {
          $timeout(clearState, 1000).then(function() {
            defer.resolve();
          });
        });
      return defer.promise;
    };
  });
