'use strict';

angular.module('videoHub')
  .directive('detailsNavBar', function() {
    return {
      'restrict': 'E',
      'templateUrl': 'shared/detailsnavbar/detailsnavbar.html',
      'transclude': true,
      scope: {
        'deleteCallback': '=',
        'sponsor': '=',
        'hideDeleteButton': '=',
        'hideSponsorButton': '=',
        'hidePublishButton': '=',
        'save': '=saveFunction',
        'published': '=',
        'toolsDropdownText': '=',
        'sponsorSelectionModalOnClose': '&',
        'dateTimeSelectorModalOnClose': '=',
        'isDirty': '='
      },
      'controller': function($scope, $rootScope, logoUrl, $location, CurrentUser, TIMEZONE_NAME, SaveButtonStateService) {
        $scope.logoUrl = logoUrl;
        $scope.currentUserName = CurrentUser.getCurrentUser();
        $scope.TIMEZONE_LABEL = moment.tz(TIMEZONE_NAME).format('z');
        $scope.data = {};
        $scope.data = SaveButtonStateService.data;
      }
    };
  });
